.container {
    max-width: 1200px;
    width: 100%;
    display: flex;
    justify-content: space-between;
    margin: 0 auto;
    flex-wrap: wrap;
}

.start {
    justify-content: flex-start;
}

.column {
    flex-direction: column;
    justify-content: flex-start;
}

.min-height {
    min-height: 75%;
}

.topPadding{
    padding: 80px 0;
}

.lite {
    background-color: #eee;
}
.transparent {
    background-color: transparent;
}