.box {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding-top: 30px;
    padding-bottom: 40px;
}

.box h2 {
    font-size: 26pt;
}

.box span {
    font-size: 14pt;
}

.box hr {
    border: 0;
    width: 100%;
    max-width: 320px;
    height: 1px;
    background-image: radial-gradient(black, transparent);
    margin: 10px 0;
}
